@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap");
* {
  font-family: "Roboto", sans-serif;
}
.container {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 600px;
  margin-top: 200px;
  background: #101820;
  padding-bottom: 30px;
  border-radius: 20px;

  transition: 3s;
}
.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 9px;
  width: 100%;
  margin-top: 30px;
}
.text {
  color: #eaeaea;
  font-size: 48px;
  font-weight: 700;
}
.underline {
  width: 61px;
  height: 6px;
  background: #eaeaea;
  border-radius: 9px;
  margin-bottom: 20px;
}
.inputs {
  display: flex;
  flex-direction: column;
  gap: 25px;
  align-items: center;
}
.input {
  display: flex;
  margin: auto;
  width: 480px;
  height: 80px;
  align-items: center;
  background: #eaeaea;
  border-radius: 6px;
}
.input img {
  margin: 0px 30px;
}
.input input {
  height: 50px;
  width: 400px;
  background: none;
  border: none;
  outline: none;
  color: #797979;
  font-size: 20px;
}
.submit-containter {
  display: flex;
  flex-direction: row;
  margin: 60px auto;
  gap: 30px;
}
.submit {
  display: flex;
  justify-content: center;
  color: #eaeaea;
  align-items: center;
  width: 200px;
  height: 55px;
  border-radius: 43px;
  background: #797979;
  font-weight: 700px;
  font-size: 20px;
  cursor: pointer;
}
.gray {
  background: #eaeaea;
  color: #797979;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 55px;
  border-radius: 43px;
  font-weight: 700px;
  font-size: 20px;
  cursor: pointer;
}
